/**
 * Message
 * @author john.gao
 */


import React from 'react';
import ReactDOM from 'react-dom';
import Message from './Message';

let messageInstance;
const prefixCls = 'wme-message';


/**
 * opts: 
 * @param {String}  title       
 * @param {String}  content     
 * @param {Array}   action      [{label:'', onClick:()=>{}]
 * @param {Number}  duration     ()
 * @param {String}  direction    rowcolumn
 * @param {bool}    isMaskClose 
 */
function newInstance(opts) {
  const { icon, title, content, action, ...restProps } = opts;
  const div = document.createElement('div');
  document.body.appendChild(div);
  ReactDOM.render(
    <Message
      prefixCls={prefixCls}
      icon={icon}
      title={title}
      content={content}
      action={action}
      {...restProps}
      onClose={() => {
        messageInstance && messageInstance.destroy();
        messageInstance = null;
      }}
    />,
    div,
  );
  return {
    destroy() {
      ReactDOM.unmountComponentAtNode(div);
      document.body.removeChild(div);
    },
  };
}

export default {
  alert(opts) {
    this.destroy();
    messageInstance = newInstance(opts);
  },
  destroy() {
    if (messageInstance) {
      messageInstance.destroy();
      messageInstance = null;
    }
  },
};
